import { useState, useEffect } from 'react';
import { ethers } from 'ethers';
import { useAccount } from 'wagmi';
import { WalletConnect } from './components/WalletConnect';
import EthRewardPoolABI from './contracts/EthRewardPool.json';
import { contractAddress, formatEther, parseEther, formatTime } from './utils/contract';
import { RoundInfo, Participant } from './types';

declare global {
  interface Window {
    ethereum: any;
  }
}

function App() {
  const { address: account, isConnected } = useAccount();
  const [contract, setContract] = useState<ethers.Contract | null>(null);
  const [roundInfo, setRoundInfo] = useState<RoundInfo>({
    roundId: 0,
    roundStart: 0,
    participants: [],
    poolBalance: '0',
    isRoundOpen: false,
    timeLeft: 0,
  });
  const [contributionAmount, setContributionAmount] = useState<string>('0.000000000000000001');
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [rewardHistory, setRewardHistory] = useState<Map<number, string>>(new Map());
  const [participantDetails, setParticipantDetails] = useState<Participant[]>([]);

  // Initialize contract when wallet is connected
  useEffect(() => {
    const initContract = async () => {
      if (isConnected && account && window.ethereum) {
        try {
          const web3Provider = new ethers.BrowserProvider(window.ethereum);
          const signer = await web3Provider.getSigner();

          const contractInstance = new ethers.Contract(
            contractAddress,
            EthRewardPoolABI,
            signer
          );
          setContract(contractInstance);
          console.log('Contract instance created for account:', account);
        } catch (error) {
          console.error('Error initializing contract:', error);
        }
      } else {
        setContract(null);
      }
    };

    initContract();
  }, [isConnected, account]);

  // Load contract data and set up polling
  useEffect(() => {
    if (contract && account) {
      loadContractData();
      const interval = setInterval(() => {
        updateRoundStatus();
      }, 1000);
      return () => clearInterval(interval);
    }
  }, [contract, account]);

  // Listen for chain changes
  useEffect(() => {
    if (window.ethereum) {
      window.ethereum.on('chainChanged', () => {
        window.location.reload();
      });
    }
  }, []);

  const loadContractData = async () => {
    if (!contract) return;

    try {
      const [roundId, roundStart, participants, poolBalance] = await Promise.all([
        contract.roundId(),
        contract.roundStart(),
        contract.getParticipants(),
        contract.getPoolBalance(),
      ]);

      const roundStartNum = Number(roundStart);
      const currentTime = Math.floor(Date.now() / 1000);
      const timeLeft = Math.max(0, roundStartNum + 600 - currentTime); // 10 minutes = 600 seconds
      const isRoundOpen = timeLeft > 0;

      setRoundInfo({
        roundId: Number(roundId),
        roundStart: roundStartNum,
        participants: participants || [],
        poolBalance: formatEther(poolBalance),
        isRoundOpen,
        timeLeft,
      });

      await loadRewardHistory(Number(roundId));
      await loadParticipantDetails(participants || []);
    } catch (error) {
      console.error('Error loading contract data:', error);
    }
  };

  const loadRewardHistory = async (currentRoundId: number) => {
    if (!contract) return;

    const history = new Map<number, string>();
    for (let i = 1; i < currentRoundId; i++) {
      try {
        const winner = await contract.getRewardRecipient(i);
        if (winner && winner !== ethers.ZeroAddress) {
          history.set(i, winner);
        }
      } catch (error) {
        console.error(`Error loading reward for round ${i}:`, error);
      }
    }
    setRewardHistory(history);
  };

  const loadParticipantDetails = async (participants: string[]) => {
    if (!contract || !account) return;

    const details: Participant[] = await Promise.all(
      participants.map(async (address) => ({
        address,
        hasJoined: await contract.hasJoined(address),
      }))
    );
    setParticipantDetails(details);
  };

  const updateRoundStatus = () => {
    setRoundInfo(prev => {
      if (prev.timeLeft <= 0) return prev;
      const newTimeLeft = prev.timeLeft - 1;
      return {
        ...prev,
        timeLeft: newTimeLeft,
        isRoundOpen: newTimeLeft > 0,
      };
    });
  };

  const joinPool = async () => {
    if (!contract || !account) {
      alert('Please connect your wallet first!');
      return;
    }

    try {
      setIsLoading(true);
      const amount = parseEther(contributionAmount);
      const tx = await contract.joinPool({ value: amount });
      await tx.wait();
      
      alert('Successfully joined the pool!');
      await loadContractData();
    } catch (error: any) {
      console.error('Error joining pool:', error);
      alert(`Error: ${error.reason || error.message}`);
    } finally {
      setIsLoading(false);
    }
  };

  const distributeReward = async () => {
    if (!contract) return;

    try {
      setIsLoading(true);
      const tx = await contract.distributeReward();
      await tx.wait();
      
      alert('Reward distributed successfully!');
      await loadContractData();
    } catch (error: any) {
      console.error('Error distributing reward:', error);
      alert(`Error: ${error.reason || error.message}`);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 to-black text-white p-4">
      <div className="max-w-6xl mx-auto">
        {/* Header */}
        <header className="flex justify-between items-center py-6 border-b border-gray-700">
          <h1 className="text-3xl font-bold bg-gradient-to-r from-blue-500 to-purple-500 bg-clip-text text-transparent">
             ETH Reward Pool
          </h1>

          <WalletConnect />
        </header>

        {/* Main Dashboard */}
        <main className="mt-8 grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* Left Column: Round Info */}
          <div className="lg:col-span-2 space-y-6">
            {/* Round Status Card */}
            <div className="bg-gray-800 rounded-xl p-6 shadow-2xl border border-gray-700">
              <h2 className="text-2xl font-bold mb-4">Current Round #{roundInfo.roundId}</h2>
              
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="space-y-4">
                  <div className="flex justify-between items-center">
                    <span className="text-gray-400">Pool Balance:</span>
                    <span className="text-2xl font-bold text-green-400">{roundInfo.poolBalance} ETH</span>
                  </div>
                  
                  <div className="flex justify-between items-center">
                    <span className="text-gray-400">Participants:</span>
                    <span className="text-xl font-semibold">{roundInfo.participants.length}</span>
                  </div>
                  
                  <div className="flex justify-between items-center">
                    <span className="text-gray-400">Status:</span>
                    <span className={`px-3 py-1 rounded-full font-semibold ${roundInfo.isRoundOpen ? 'bg-green-500/20 text-green-400' : 'bg-red-500/20 text-red-400'}`}>
                      {roundInfo.isRoundOpen ? 'Open' : 'Closed'}
                    </span>
                  </div>
                </div>
                
                <div className="space-y-4">
                  <div className="flex justify-between items-center">
                    <span className="text-gray-400">Time Remaining:</span>
                    <span className="text-2xl font-bold text-yellow-400 font-mono">
                      {formatTime(roundInfo.timeLeft)}
                    </span>
                  </div>
                  
                  <div className="flex justify-between items-center">
                    <span className="text-gray-400">Min Contribution:</span>
                    <span className="font-mono">0.000000000000000001 ETH</span>
                  </div>
                </div>
              </div>
              
              {/* Progress Bar */}
              <div className="mt-6">
                <div className="flex justify-between text-sm text-gray-400 mb-2">
                  <span>Round Progress</span>
                  <span>{Math.max(0, 100 - (roundInfo.timeLeft / 600 * 100)).toFixed(1)}%</span>
                </div>
                <div className="h-2 bg-gray-700 rounded-full overflow-hidden">
                  <div 
                    className="h-full bg-gradient-to-r from-blue-500 to-purple-500 transition-all duration-1000"
                    style={{ width: `${Math.max(0, 100 - (roundInfo.timeLeft / 600 * 100))}%` }}
                  ></div>
                </div>
              </div>
            </div>

            {/* Actions Card */}
            <div className="bg-gray-800 rounded-xl p-6 shadow-2xl border border-gray-700">
              <h2 className="text-2xl font-bold mb-4">Actions</h2>
              
              <div className="space-y-4">
                <div className="space-y-2">
                  <label className="block text-gray-400">Contribution Amount (ETH)</label>
                  <input
                    type="text"
                    value={contributionAmount}
                    onChange={(e) => setContributionAmount(e.target.value)}
                    className="w-full bg-gray-900 border border-gray-700 rounded-lg px-4 py-3 text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
                    placeholder="0.000000000000000001"
                  />
                  <p className="text-sm text-gray-500">Minimum: 0.000000000000000001 ETH</p>
                </div>
                
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <button
                    onClick={joinPool}
                    disabled={!roundInfo.isRoundOpen || isLoading || !account}
                    className="w-full bg-gradient-to-r from-green-600 to-emerald-600 hover:from-green-700 hover:to-emerald-700 disabled:from-gray-600 disabled:to-gray-700 disabled:cursor-not-allowed text-white px-6 py-4 rounded-lg font-semibold text-lg transition-all duration-300 transform hover:scale-105 disabled:hover:scale-100"
                  >
                    {isLoading ? 'Processing...' : 'Join Pool'}
                  </button>
                  
                  <button
                    onClick={distributeReward}
                    disabled={roundInfo.isRoundOpen || isLoading || !account}
                    className="w-full bg-gradient-to-r from-red-600 to-pink-600 hover:from-red-700 hover:to-pink-700 disabled:from-gray-600 disabled:to-gray-700 disabled:cursor-not-allowed text-white px-6 py-4 rounded-lg font-semibold text-lg transition-all duration-300 transform hover:scale-105 disabled:hover:scale-100"
                  >
                    {isLoading ? 'Processing...' : 'Distribute Reward'}
                  </button>
                </div>
              </div>
            </div>

            {/* Participants Card */}
            <div className="bg-gray-800 rounded-xl p-6 shadow-2xl border border-gray-700">
              <h2 className="text-2xl font-bold mb-4">Participants ({participantDetails.length})</h2>
              
              <div className="max-h-64 overflow-y-auto">
                <div className="space-y-2">
                  {participantDetails.length > 0 ? (
                    participantDetails.map((participant, index) => (
                      <div
                        key={index}
                        className={`p-3 rounded-lg ${account && participant.address.toLowerCase() === account.toLowerCase() ? 'bg-blue-500/20 border border-blue-500/30' : 'bg-gray-900/50'}`}
                      >
                        <div className="flex justify-between items-center">
                          <div className="flex items-center space-x-3">
                            <div className="w-8 h-8 rounded-full bg-gradient-to-r from-blue-500 to-purple-500 flex items-center justify-center">
                              <span className="text-sm font-bold">{index + 1}</span>
                            </div>
                            <span className="font-mono text-sm">
                              {participant.address.slice(0, 8)}...{participant.address.slice(-6)}
                              {account && participant.address.toLowerCase() === account.toLowerCase() && (
                                <span className="ml-2 text-xs bg-blue-500 px-2 py-1 rounded">You</span>
                              )}
                            </span>
                          </div>
                          <span className={`px-2 py-1 text-xs rounded ${participant.hasJoined ? 'bg-green-500/20 text-green-400' : 'bg-gray-700 text-gray-400'}`}>
                            {participant.hasJoined ? 'Joined' : 'Not Joined'}
                          </span>
                        </div>
                      </div>
                    ))
                  ) : (
                    <div className="text-center py-8 text-gray-500">
                      No participants yet. Be the first to join!
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>

          {/* Right Column: History & Info */}
          <div className="space-y-6">
            {/* Reward History */}
            <div className="bg-gray-800 rounded-xl p-6 shadow-2xl border border-gray-700">
              <h2 className="text-2xl font-bold mb-4"> Reward History</h2>
              
              <div className="space-y-3 max-h-80 overflow-y-auto">
                {Array.from(rewardHistory.entries()).map(([roundId, winner]) => (
                  <div key={roundId} className="p-3 bg-gray-900/50 rounded-lg hover:bg-gray-900 transition-colors">
                    <div className="flex justify-between items-center mb-2">
                      <span className="font-bold text-blue-400">Round #{roundId}</span>
                      <span className="text-xs text-gray-500">Completed</span>
                    </div>
                    <div className="text-sm">
                      <div className="text-gray-400 mb-1">Winner:</div>
                      <div className="font-mono text-xs break-all">{winner}</div>
                    </div>
                  </div>
                ))}
                
                {rewardHistory.size === 0 && (
                  <div className="text-center py-8 text-gray-500">
                    No reward history yet. Be the first winner!
                  </div>
                )}
              </div>
            </div>

            {/* Contract Info */}
            <div className="bg-gray-800 rounded-xl p-6 shadow-2xl border border-gray-700">
              <h2 className="text-2xl font-bold mb-4"> Contract Info</h2>
              
              <div className="space-y-3">
                <div className="flex justify-between">
                  <span className="text-gray-400">Contract Address:</span>
                  <a 
                    href={`https://sepolia.etherscan.io/address/${contractAddress}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="text-blue-400 hover:text-blue-300 font-mono text-sm"
                  >
                    {contractAddress.slice(0, 6)}...{contractAddress.slice(-4)}
                  </a>
                </div>
                
                <div className="flex justify-between">
                  <span className="text-gray-400">Network:</span>
                  <span>Sepolia Testnet</span>
                </div>
                
                <div className="flex justify-between">
                  <span className="text-gray-400">Round Duration:</span>
                  <span>10 minutes</span>
                </div>
                
                <div className="flex justify-between">
                  <span className="text-gray-400">Current Account:</span>
                  <span className="font-mono text-sm">
                    {account ? `${account.slice(0, 6)}...${account.slice(-4)}` : 'Not connected'}
                  </span>
                </div>
              </div>
            </div>

            {/* Instructions */}
            <div className="bg-gradient-to-br from-blue-900/30 to-purple-900/30 rounded-xl p-6 shadow-2xl border border-blue-500/30">
              <h2 className="text-xl font-bold mb-3"> How It Works</h2>
              
              <ul className="space-y-2 text-sm">
                <li className="flex items-start">
                  <span className="text-green-400 mr-2"></span>
                  <span>Connect your MetaMask wallet</span>
                </li>
                <li className="flex items-start">
                  <span className="text-green-400 mr-2"></span>
                  <span>Join the pool with minimum 0.000000000000000001 ETH</span>
                </li>
                <li className="flex items-start">
                  <span className="text-green-400 mr-2"></span>
                  <span>Wait for round to end (10 minutes)</span>
                </li>
                <li className="flex items-start">
                  <span className="text-green-400 mr-2"></span>
                  <span>One random participant wins entire pool!</span>
                </li>
                <li className="flex items-start">
                  <span className="text-yellow-400 mr-2"></span>
                  <span>Any participant can trigger the reward distribution</span>
                </li>
              </ul>
            </div>
          </div>
        </main>

        {/* Footer */}
        <footer className="mt-8 pt-6 border-t border-gray-700 text-center text-gray-500 text-sm">
          <p>Build by Kahuna</p>
          <p className="mt-2">@Copyright reserved</p>
        </footer>
      </div>
    </div>
  );
}

export default App;