import { useAccount, useConnect, useDisconnect } from 'wagmi'

export function WalletConnect() {
  const { address, isConnected } = useAccount()
  const { connect, connectors } = useConnect()
  const { disconnect } = useDisconnect()

  const handleConnect = () => {
    // Find MetaMask connector first, fallback to first available connector
    const metaMaskConnector = connectors.find(
      (connector) => connector.name.toLowerCase().includes('metamask')
    )
    const connectorToUse = metaMaskConnector || connectors[0]

    if (connectorToUse) {
      connect({ connector: connectorToUse })
    }
  }

  if (isConnected && address) {
    return (
      <div className="flex items-center space-x-4">
        <div className="bg-gray-800 px-4 py-2 rounded-lg">
          <span className="text-gray-400">Connected:</span>
          <span className="ml-2 font-mono text-sm">
            {address.slice(0, 6)}...{address.slice(-4)}
          </span>
        </div>
        <button
          onClick={() => disconnect()}
          className="bg-red-600 hover:bg-red-700 text-white px-4 py-2 rounded-lg font-semibold transition-all"
        >
          Disconnect
        </button>
      </div>
    )
  }

  return (
    <button
      onClick={handleConnect}
      className="bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700 text-white px-6 py-3 rounded-lg font-semibold transition-all duration-300 transform hover:scale-105"
    >
      Connect Wallet
    </button>
  )
}

